'use client';
import React, { useState, useEffect } from 'react';
import { ArrowRight, ChevronLeft, ChevronRight, Mountain } from 'lucide-react';
import threePassesImage from '../assets/images/threepasses.jpeg';
import abcImage from '../assets/images/abc.jpeg';
import ebcImage from '../assets/images/ebc.jpeg';
import gokyoImage from '../assets/images/gokyo.jpeg';
import thoranglaImage from '../assets/images/thorangla-pass.jpeg';

export interface HeroProps {
  onExploreTreks?: () => void;
  onWatchStory?: () => void;
}

const HeroComponent: React.FC<HeroProps> = ({ onExploreTreks }) => {
  
  // Hero slides with titles
  const heroSlides = [
    { image: threePassesImage, title: 'TREKKING TO THE MOUNTAINS', subtitle: 'Discover the Beauty of Himalayas' },
    { image: ebcImage, title: 'EVEREST BASE CAMP', subtitle: 'Journey to the Roof of the World' },
    { image: abcImage, title: 'ANNAPURNA CIRCUIT', subtitle: 'Experience the Ultimate Adventure' },
    { image: gokyoImage, title: 'GOKYO LAKES', subtitle: 'Witness Nature\'s Masterpiece' },
    { image: thoranglaImage, title: 'THORONG LA PASS', subtitle: 'Conquer the Mighty Peaks' }
  ];
  
  const [currentSlide, setCurrentSlide] = useState(0);
  
  // Auto-rotate slides every 5 seconds
  useEffect(() => {
    const interval = setInterval(() => {
      setCurrentSlide((prev) => (prev + 1) % heroSlides.length);
    }, 5000);
    return () => clearInterval(interval);
  }, [heroSlides.length]);

  const nextSlide = () => setCurrentSlide((prev) => (prev + 1) % heroSlides.length);
  const prevSlide = () => setCurrentSlide((prev) => (prev - 1 + heroSlides.length) % heroSlides.length);
  
  return (
    <section id="home" className="relative h-screen flex items-center justify-center overflow-hidden">
      {/* Background Images with Smooth Transitions */}
      {heroSlides.map((slide, index) => (
        <div 
          key={index}
          className={`absolute inset-0 transition-opacity duration-1000 ${
            index === currentSlide ? 'opacity-100' : 'opacity-0'
          }`}
        >
          <div 
            className="absolute inset-0 bg-cover bg-center bg-no-repeat"
            style={{
              backgroundImage: `url("${typeof slide.image === 'string' ? slide.image : slide.image.src}")`
            }}
          />
          <div className="absolute inset-0 bg-gradient-to-b from-black/60 via-black/40 to-black/60" />
        </div>
      ))}

      {/* Navigation Arrows */}
      <button
        onClick={prevSlide}
        className="absolute left-4 md:left-8 top-1/2 -translate-y-1/2 z-20 bg-white/20 hover:bg-primary-500 backdrop-blur-sm p-3 rounded-full transition-all duration-300 group"
        aria-label="Previous slide"
      >
        <ChevronLeft className="w-6 h-6 text-white" />
      </button>
      <button
        onClick={nextSlide}
        className="absolute right-4 md:right-8 top-1/2 -translate-y-1/2 z-20 bg-white/20 hover:bg-primary-500 backdrop-blur-sm p-3 rounded-full transition-all duration-300 group"
        aria-label="Next slide"
      >
        <ChevronRight className="w-6 h-6 text-white" />
      </button>

      {/* Content */}
      <div className="relative z-10 text-center text-white px-4 max-w-6xl mx-auto">
        <div className="mb-6 flex justify-center">
          <Mountain className="w-16 h-16 text-primary-500 animate-pulse" />
        </div>
        
        <h1 className="text-5xl md:text-7xl lg:text-8xl font-display font-bold mb-4 tracking-wide uppercase animate-fade-in-up">
          {heroSlides[currentSlide].title}
        </h1>
        
        <p className="text-xl md:text-2xl lg:text-3xl font-body mb-12 text-gray-200 animate-fade-in-up" style={{ animationDelay: '0.2s' }}>
          {heroSlides[currentSlide].subtitle}
        </p>

        {/* CTA Button */}
        <div className="flex justify-center animate-fade-in-up" style={{ animationDelay: '0.4s' }}>
          <button 
            onClick={onExploreTreks}
            className="group bg-primary-500 hover:bg-primary-600 text-white px-12 py-5 text-xl font-display font-bold uppercase tracking-wider transition-all duration-300 flex items-center space-x-3 shadow-2xl hover:shadow-primary-500/50 hover:scale-105 border-4 border-white/20"
          >
            <span>START JOURNEY</span>
            <ArrowRight className="w-6 h-6 group-hover:translate-x-2 transition-transform" />
          </button>
        </div>
      </div>

      {/* Slide Indicators */}
      <div className="absolute bottom-8 left-1/2 transform -translate-x-1/2 flex space-x-3 z-20">
        {heroSlides.map((_, index) => (
          <button
            key={index}
            onClick={() => setCurrentSlide(index)}
            className={`transition-all duration-300 ${
              index === currentSlide 
                ? 'w-12 h-3 bg-primary-500' 
                : 'w-3 h-3 bg-white/50 hover:bg-white/75'
            } rounded-full`}
            aria-label={`Go to slide ${index + 1}`}
          />
        ))}
      </div>
    </section>
  );
};

export default HeroComponent;
